import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Menu } from 'antd';
import './../../../styles/settings.less';
import { setTheme } from './../../../redux/actions/theme';
import { sendUpdateTheme } from './../../../redux/actions/websocket';
import { shallowEqual } from 'shouldcomponentupdate-children';
import Loadable from 'react-loadable';
import Loading from './../loading/loading';
import PerfectScrollbar from 'react-perfect-scrollbar';

const ThemeSubContainer = Loadable({
	loader: () => import('./../../../components/settings-components/theme-subcontainer'),
	loading: Loading
});

class Settings extends Component {
	constructor(props) {
		super(props);

		this.state = {
			colors: ''
		};
		console.log(this.props.theme);
	}

	changeSubContainer = () => {};

	shouldComponentUpdate(nextProps, nextState) {
		return shallowEqual(this.props, nextProps, this.state, nextState);
	}

	render() {
		return (
			<PerfectScrollbar>
				<div className="settings-container">
					<div style={{ gridColumn: '1/1' }} className="settings-menu-container">
						<Menu theme={this.props.theme.dark} mode="inline" defaultSelectedKeys={[ '1' ]}>
							<Menu.Item key="1">Configuracoes de Tema</Menu.Item>
							<Menu.Item key="2">Configuracoes Basicas</Menu.Item>
						</Menu>
					</div>
					<div style={{ gridColumn: '2/2' }} className="settings-subcontainer-container">
						<ThemeSubContainer
							theme={this.props.theme}
							setTheme={this.props.setTheme}
							saveTheme={this.props.sendUpdateTheme}
						/>
					</div>
				</div>
			</PerfectScrollbar>
		);
	}
}

const mapStateToProps = (state) => {
	return {
		theme: state.theme
	};
};

const mapDispatchToProps = {
	setTheme,
	sendUpdateTheme
};

export default connect(mapStateToProps, mapDispatchToProps)(Settings);



